.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  border: solid 1px lightgray;
  border-radius: 10px;
  margin: 10px;
  padding: 10px;
}

.flexColumn {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  padding: 10px;
}

.country {
  width: 100px;
  font-size: large;
}

.population {
  width: 300px;
  font-size: large;
}
